import { Button, ButtonStyleTypes } from '../components/Button/Button';
import { Field } from '../components/Field/Field';
import { FileForm } from '../components/Form/FileForm';
import { Form } from '../components/Form/Form';
import { FileInput } from '../components/Input/FileInput';
import { Input } from '../components/Input/Input';
import { Modal } from '../components/Modal/Modal';
import { ChatsController } from '../controllers/ChatsController';

export type ModalNames =
  | 'delete-chat'
  | 'add-user'
  | 'delete-user'
  | 'change-avatar'
  | 'create-chat';

export const modalsSwitcher = (
  onCloseModal: () => void,
  chatId: number,
  controller: ChatsController,
  modalName?: ModalNames | null
) => {
  const loginInput = new Input({
    name: 'login',
    inputStyle: 'inputField__input',
  });
  const loginField = new Field({
    input: loginInput,
    label: '',
    name: 'login',
  });

  const addUserForm = new Form<{ login: 'login' }>({
    fields: [loginField],
    inputs: [loginInput],
    submitBtn: new Button({ label: ' ', stylesType: ButtonStyleTypes.Submit }),
    submit: async ({ login }) => {
      await controller.addUser(login, chatId);
      onCloseModal();
    },
  });

  const deleteUserForm = new Form<{ login: 'login' }>({
    fields: [loginField],
    inputs: [loginInput],
    submitBtn: new Button({ label: ' ', stylesType: ButtonStyleTypes.Submit }),
    submit: async ({ login }) => {
      await controller.deleteUser(login, chatId);
      onCloseModal();
    },
  });

  const fileInput = new FileInput({
    type: 'file',
    name: 'avatar',
    id: 'avatar',
    inputStyle: 'modal__fileInput',
  });
  const fileField = new Field({
    input: fileInput,
    label: ' ',
    name: 'avatar',
    labelStyle: 'modal__fileInputLabel',
  });

  const fileForm = new FileForm<{ avatar: 'avatar' }>({
    fields: [fileField],
    inputs: [fileInput],
    submit: async ({ avatar }) => {
      if (avatar) {
        const formData = new FormData();
        formData.append('avatar', avatar);
        formData.append('chatId', chatId.toString());
        await controller.editAvatar(formData);
      }
      onCloseModal();
    },
    submitBtn: new Button({ label: ' ', stylesType: ButtonStyleTypes.Submit }),
    formClass: 'modal__fileForm',
  });

  const createChatInput = new Input({
    name: 'chatName',
    inputStyle: 'inputField__input',
  });
  const createChatField = new Field({
    input: createChatInput,
    label: ' ',
    name: 'chatName',
  });
  const createChatForm = new Form<{ chatName: 'chatName' }>({
    fields: [createChatField],
    inputs: [createChatInput],
    submit: async ({ chatName }) => {
      await controller.createChat(chatName);
      onCloseModal();
    },
    submitBtn: new Button({ label: ' ', stylesType: ButtonStyleTypes.Submit }),
    formClass: 'modal__fileForm',
  });

  switch (modalName) {
    case 'delete-chat':
      return new Modal({
        isOpen: true,
        content: [new Button({ label: '', stylesType: ButtonStyleTypes.Submit })],
        header: '?',
        onClose: onCloseModal,
      });
    case 'add-user':
      return new Modal({
        isOpen: true,
        header: ' ',
        content: [addUserForm],
        onClose: onCloseModal,
      });
    case 'delete-user':
      return new Modal({
        isOpen: true,
        header: ' ',
        content: [deleteUserForm],
        onClose: onCloseModal,
      });
    case 'change-avatar':
      return new Modal({
        isOpen: true,
        header: ' ',
        content: [fileForm],
        onClose: onCloseModal,
      });
    case 'create-chat':
      return new Modal({
        isOpen: true,
        header: ' ',
        content: [createChatForm],
        onClose: onCloseModal,
      });
    default:
      return undefined;
  }
};
